import {request} from './request'

export function getOrder() {
  return request({
    //url:'/test/getBanners'
	//url:'/vue/VueProjectData/home/product.php'
	url:'/vue/VueProjectData/manager/goods.php'
  });
}

//
export function getSaleOrders(typeid=0,page=1,date=0) {
  return request({
	url:'/vue/VueProjectData/manager/saleorder.php',
    params:{
		typeid,
		page,
		date
    }
  });
}

