import {getSaleOrders,getPurchaseList,getStockRoomList} from 'network/order'
import {getCustomerList} from 'network/customer'
import {getClassifyData}  from 'network/classify'

export default{
		//
		addOrders(context,payload){
			return new Promise((resolve)=>{
				context.commit('addToOrders',payload)
				resolve("")
			})
		},
		//
		deleteOrder(context,payload){
			return new Promise((resolve)=>{
				//context.commit('deleteOrder',payload)
				//
				let productIndex=context.state.OrdersList[payload.type].list.findIndex(item=>item.oid===payload.id)
				//console.log(context.state.OrdersList['tohdayOrder'].list[0])
				//
				if(productIndex==-1){
					resolve("")
				}else{
					//-mutations
					context.state.OrdersList[payload.type].list.splice(productIndex,1);
					resolve("")
				}
					
			})
		},
		//
		addRole(context,payload){
			return new Promise((resolve)=>{
				//console.log(context,payload)
				//-actions
				//context.state.collectList[payload.type].list.push(productIndex,1);
				//
				let oldProduct=context.state.collectList[payload.type].list.find(item=>item.oid===payload.oid)

				if(oldProduct){
					//+1
					context.commit('updateRole',oldProduct)
					resolve("+1")
				}else{
					payload.count=1
					context.commit('addRole',payload)
					resolve("")
				}

			})
		},
		//
		getDaySaleOrders(context,payload){
			//payload.id,payload.page
			getSaleOrders(payload.typeid,payload.page,payload.date).then(res=>{
				context.commit(payload.type,res)
			})
		},
		//-getStockRoomList
		getStockRoomList(context,payload){
			getStockRoomList(payload.statusid,payload.date).then(res=>{
				context.commit(payload.type,res)
			})
		},
		//-getStockRoomList
		getShipmentList(context,payload){
			getStockRoomList(payload.statusid,payload.date).then(res=>{
				context.commit(payload.type,res)
			})
		},
		//
		getDayPurchaseList(context,payload){
			getPurchaseList(payload.id,payload.page,payload.date).then(res=>{
				context.commit(payload.type,res)
			})
		},

		//
		customerList(context,payload){
			getCustomerList(payload.page).then(res=>{
				context.commit(payload.type,res)
			})
		},
		//
		getClassifyData(context,payload){
			getClassifyData(payload.cid).then(res=>{
				context.commit('getClassifyData',res)
			})
		},
		//
		addOrderProduct(context,payload){
			return new Promise((resolve)=>{
				let oldProduct=context.state.orderProductList.find(item=>item.id===payload.id)
				
				if(oldProduct){
					//+1
					context.commit('updateOrderProduct',oldProduct)
					resolve("+1")
				}else{
					payload.count=1
					context.commit('addOrderProduct',payload)
					resolve("")
				}

			})
		},
		//--
		cutOrderProduct(context,payload){
			return new Promise((resolve)=>{
				//console.log(context,payload)
				//-actions
				//context.state.collectList[payload.type].list.push(productIndex,1);
				//-
				let oldProduct=context.state.orderProductList.find(item=>item.id===payload.id)		//
				let index=context.state.orderProductList.findIndex(item=>item.id===payload.id)		//
				//-00 orderProductList[?]
				//- 0-
				if(oldProduct){
					if(oldProduct.count<=1){								//0
						context.state.orderProductList.splice(index,1)		//
						resolve("")
					}else{
					//-1
						context.commit('cutOrderProduct',oldProduct)			//
						resolve("-1")
					}
				}else{
					resolve("")
				}

			})
		},
		//
		addTest(context,payload){
			return new Promise((resolve)=>{
				let oldProduct=context.state.testArr.find(item=>item.id===payload.id)		//
				if(oldProduct){
					context.commit('addTest',oldProduct)			//
					resolve("+1+1")
				}else{
					payload.count=1
					context.commit('addOrderProduct',payload)
					resolve("")
				}

			})
		},
		// //  service
		setToken(context,obj){
			context.commit('setToken',obj)
		},
		setUser(context, obj){
			context.commit('setUser',obj)
		},
}